@import "../../global.scss";

.services {
  background-color: $mainColor;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  h1 {
    margin-bottom: 600px;
    color: $secondaryColor;
    font-size: 50px;
    @include mobile {
      margin-bottom: 10;
      font-size: 40px;
    }
  }
  hr {
    position: absolute;
    top: 110px;
    width: 23%;
    height: 2px;
    background-color: $secondaryColor;
    @include mobile {
      display: none;
    }
  }

  .arrow {
    height: 50px;
    position: absolute;

    @include mobile {
      display: none;
    }
    &.left {
      left: 100px;
      cursor: pointer;
    }
    &.right {
      right: 100px;
      cursor: pointer;
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height: 80vh;
      flex-direction: column;
      justify-content: center;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;
        background-color: $secondaryColor;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile {
          width: 80%;
          height: 150px;
          margin: 15px 0;
        }

        .left {
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            color: $mainColor;

            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: $mainColor;
              display: flex;
              align-items: center;
              justify-content: center;
              overflow: hidden;

              @include mobile {
                width: 27px;
                height: 27px;
              }

              span {
                color:$secondaryColor;
              }
            }
            h2 {
              font-size: 20px;
              @include mobile {
                font-size: 13px;
              }
            }
            p {
              font-size: 13px;
              @include mobile {
                display: none;
              }
            }
            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }
        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          img {
            width: 400px;
            transform: rotate(-10deg);
          }
        }
      }
    }
  }
}
